body {
  background-color: #f2f2f2;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

#chat-window {
  background-color: #fff;
  border-radius: 8px;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

#chat-messages {
  max-height: 300px;
  overflow-y: auto;
}

.user-message, .other-message {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.user-message {
  color: #fff;
  background-color: #4caf50;
  border-radius: 15px;
  margin-bottom: 10px;
  padding: 10px 15px;
}

.other-message {
  color: #000;
  background-color: #cfe2f3;
  border-radius: 15px;
  align-self: flex-start;
  margin-bottom: 10px;
  padding: 10px 15px;
}

#input-container {
  margin-top: 20px;
  margin-bottom: 40px;
  display: flex;
}

#message-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex: 1;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

#send-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

#send-button:hover {
  background-color: #45a049;
}

.delete-button {
  color: #fff;
  cursor: pointer;
  background-color: tomato;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

